<template>
  <div class="about">
    <h1>This is an about page</h1>
    <button @click="toast">toast</button>
    <button @click="imagePick">imagePick</button>
    <button @click="imagePickMulti">imagePickMulti</button>
    <button @click="pressButtonOnOff">pressButtonOnOff</button>
    <button @click="imageCrop">imageCrop</button>
    <button @click="recordingSound">recordingSound</button>
    <button @click="recordingVideo">recordingVideo</button>
    <img :src="imgSrc"/>
    <div v-for="v in result" :key="v">
      <div style="color:red">{{v.path}}</div>
      <div>{{v.bytes}}</div>
    </div>
  </div>
</template>
<script lang="ts">
import Vue from 'vue'
export default Vue.extend({
  data(){
    return {
        result:[],
        imgSrc:'',
    }
  },
  methods:{
    toast(){
      this.$KPG_Toaster('提示框');
    },
    imagePick(){
      this.$KPG_ImagePick('camera').then(flies=>{
        console.log(flies[0].type);
        console.log(flies[0].bytes);
        let imgBlob = new Blob([flies[0].bytes],{type:flies[0].type});
        console.log(imgBlob);
        this.imgSrc = window.URL.createObjectURL(imgBlob);
      })
    },
    imagePickMulti(){
      this.$KPG_ImagePickMulti(9).then(flies=>{
        console.log(flies)
        this.result = flies;
      })
    },
    pressButtonOnOff(){
      this.$KPG_PressButtonOnOff('on')
    },
    imageCrop(){
      if(this.result&&this.result.length>0){
        this.$KPG_ImageCrop(this.result[0]).then(flies=>{
            this.result = flies;
        })
      }
    },
    recordingSound(){
      this.$KPG_RecordingSound().then(flies=>{

      })
    },
    recordingVideo(){
      this.$KPG_RecordingVideo().then(flies=>{

      })
    }
  }
})
</script>